.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* App.css */

.app-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0px;
}

.app-header {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 20px;
}

.app-main {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.team-box {
  background-color: #fff;
  border: 2px solid #000000;
  border-radius: 8px;
  padding: 20px;
  margin: 10px;
  width: 200px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.team-box h3 {
  color: #333;
}

.team-box p {
  margin: 5px 0;
  color: #666;
}
