/* FormComponent.css */

.form-container {
    max-width: 340px;
    margin: 0px;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 10 10 10px rgba(0, 0, 0, 0.1);
    float: right;
    
  }
  
  h2 {
    text-align: center;
    color: #333;
  }
  
  form {
    display: flex;
    margin: 0;
    flex-direction: column;
  }
  
  label {
    margin-bottom: 10px;
  }
  
  input {
    width: 100%;
    padding: 5px;
    margin-top: 5px;
    margin-bottom: 10px;
  }
  
  button {
    background-color: #007bff;
    color: #fff;
    padding: 10px;
    cursor: pointer;
    border: none;
    border-radius: 4px;
  }
  